import { TranslateService } from "@ngx-translate/core";
import { Component, ViewContainerRef, OnInit, OnDestroy } from "@angular/core";
import { Subscription } from "rxjs";
import { Router, NavigationEnd } from "@angular/router";
import { filter } from "rxjs/operators";
import { StyleService } from "app/shared/services/StyleService.service";
import { AppStrings } from "app/shared/data/app-strings";
import { LanguageService } from "app/shared/services/language.service";
import { AnimationOptions } from "ngx-lottie";
import { JsonAssets } from "app/shared/data/app-assets";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
})
export class AppComponent implements OnInit, OnDestroy {
  subscription: Subscription;
  loading = true;

  constructor(
    private router: Router,
    private appStrings: AppStrings,
    private languageService: LanguageService
  ) {}

  ngOnInit() {
    this.subscription = this.router.events
      .pipe(filter((event) => event instanceof NavigationEnd))
      .subscribe(() => window.scrollTo(0, 0));
    let currentLang: string = this.languageService.currentLang;
    this.languageService.changeLanguage(currentLang);

    setTimeout(() => {
      this.loading = false;
    }, 3500);
  }

  options: AnimationOptions = {
    path: JsonAssets.loadingJson,
  };

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
  }
}
