import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { AuthService } from "app/shared/services/auth/auth.service";
import { MessagesService } from "app/shared/services/messages.service";
import { IAddEditCourseToCart } from "app/student/models/shopping-cart";
import { ShoppingCartService } from "app/student/services/shopping-cart.service";
import { NgxSpinnerService } from "ngx-spinner";

@Component({
  selector: "app-add-to-cart-btn",
  templateUrl: "./add-to-cart-btn.component.html",
  styleUrls: ["./add-to-cart-btn.component.scss"],
})
export class AddToCartBtnComponent implements OnInit {
  constructor(
    public authService: AuthService,
    public msg: MessagesService,
    private shoppingCartService: ShoppingCartService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {}

  ngOnInit(): void {}

  addToCart(courseId: number) {
    if (!this.authService.isUserLoggedIn()) {
      this.router.navigateByUrl("auth/login");
      return;
    }

    let cartData: IAddEditCourseToCart = {
      id: 0,
      subject_id: courseId,
      diploma_id: null,
    };

    this.spinner.show();
    this.shoppingCartService
      .addEditUserCartCourses(cartData)
      .subscribe((res: any) => {
        if (res.returnValue == 1) {
          this.msg.success("course.AddToCartSuccess");
          // change add to cart into in cart
          // this.courses.map((course) => {
          //   if (course.subject_id == courseId) course.isCard = true;
          // });
        }
        this.spinner.hide();
      });
  }
}
