import { HttpClient } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IAddEditCourseToCart,
  RatetCourseRequest,
} from "../models/shopping-cart";

@Injectable({
  providedIn: "root",
})
export class ShoppingCartService extends AppClientService {
  constructor(private http: HttpClient) {
    super(http);
  }

  // get all courses  of user
  getAllUserCartCourses() {
    return this.get(`UserCard/getAllUserCard`);
  }

  // get all user paied courses
  getUserPaidCourses() {
    return this.get(`UserCard/getMyPaidCourses`);
  }

  //add/edit course to cart
  addEditUserCartCourses(data: IAddEditCourseToCart) {
    return this.post(`userCard/AddEditCardUser`, data);
  }

  //delete course from cart
  deleteCourseFromCart(id: number) {
    return this.delete(`userCard/DeleteUserCard/${id}`);
  }
  // buy course
  buyCourse(data: any) {
    return this.post(`api/subjectCategory/CoursePayment`, data);
  }
  //Ratecourse from cart
  rateCourse(body: RatetCourseRequest) {
    return this.post(`SubjectRate/AddEditRateToSubject`, body);
  }
}
